// quote sorter parent styles
.wp-block-prc-block-quote-sorter {
	.wp-block-prc-block-quote-sorter-dynamic-text {
		font-style: italic;
		font-family: var(--wp--preset--font-family--default);
		font-size: var(--wp--preset--font-size--large);
		line-height: 160%;
	}

	.wp-block-prc-block-quote-sorter-search-bar {
		.ui.input input {
			height: 44px !important;
			border-radius: 0;
			font-size: var(--wp--preset--font-size--small);
		}
	}

	.wp-block-prc-block-quote-sorter-dropdown {
		&.is-style-list-inline {
			.ui.basic.button {
				padding: 0.5em 1em;
				box-shadow: none;
				margin-left: 0px;
				border-radius: 0;
				font-size: var(--wp--preset--font-size--medium);
				&.active {
					color: var(--wp--preset--color--white) !important;
					background-color: #5b7344 !important;
					font-weight: 500 !important;
				}
			}
		}
	}
	.show-more {
		display: grid;
		place-items: center;
		.show-more__button {
			width: 150px;
			border-radius: 0;
			background-color: #fff;
			font-family: var(--wp--preset--font-family--sans-serif);
			font-size: 15px;
			font-weight: 400;
			border: 1px solid #afafaf;
			color: rgba(0, 0, 0, 0.6) !important;
			&:hover {
				background-color: #dadbdb !important;
			}
			&.hidden {
				display: none;
			}
		}
	}
}
